import { Header } from "app/components/header/header.component";
import * as styles from "app/components/app.module.css";
import { Content } from "app/components/content/content.component";
import { Menu } from "app/components/menu/menu.component";
import { numberService } from "features/number/services/number.service";
import { defineComponent } from "libraries/ui/define-component";
import { ScreenSlider } from "libraries/screen-slider";
import { div } from "libraries/ui/html-elements";

interface IAppProps {
  contentScreenSlider: ScreenSlider;
  appScreenSlider: ScreenSlider;
}

class AppComponent extends HTMLElement {
  private props!: IAppProps;

  private onFirstScreenButtonClick(): void {
    this.props.contentScreenSlider.goToScreen(0);
  }

  private onSecondScreenButtonClick(): void {
    this.props.contentScreenSlider.goToScreen(1);
  }

  private onThirdScreenButtonClick(): void {
    this.props.contentScreenSlider.goToScreen(2);
  }

  private onNextScreenButtonClick(): void {
    this.props.appScreenSlider.goToTemporaryScreen(
      div(" ", {
        class: styles.screen,
        id: "next-screen",
      })
    );
  }

  private connectedCallback(): void {
    this.render();

    const appScreens = [
      div(
        new Header({
          class: styles.header,
        }), //
        new Content({
          greeting: "hello world",
          getRandomNumber: numberService.getRandomNumber,
          class: styles.content,
          onNextScreenClick: () => {
            this.onNextScreenButtonClick();
          },
          screenSlider: this.props.contentScreenSlider,
        }), //
        new Menu({
          class: styles.menu,
          onFirstScreenButtonClick: () => {
            this.onFirstScreenButtonClick();
          },
          onSecondScreenButtonClick: () => {
            this.onSecondScreenButtonClick();
          },
          onThirdScreenButtonClick: () => {
            this.onThirdScreenButtonClick();
          },
        }),
        {
          class: `${styles.screen} ${styles.homeScreen}`,
        }
      ),
    ];
    this.props.appScreenSlider.init("#app-screen-container", appScreens);
  }

  private disconnectedCallback() {
    this.props.appScreenSlider.destroy();
    this.props.contentScreenSlider.destroy();
  }

  private template(): HTMLElement {
    return div({
      id: "app-screen-container",
      class: styles.appScreenContainer,
    });
  }

  private render(): void {
    this.replaceChildren(this.template());
  }

  public init(props: IAppProps) {
    this.props = props;
  }
}

export const App = defineComponent<AppComponent, IAppProps>("app-root", AppComponent);
